@import "../vars.scss";
.page-top{
    padding: 1.19em 0 2.86em;
}

.breadcrumbs {
    margin-bottom: .75em;
    &:last-child{
        margin-bottom: 0;
    }
    &__item {
        position: relative;
        display: inline;
        color: #828282;
        &:not(:first-child) {
            margin-left: 1.875em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: 2em;
                height: .9375em;
                background-image: url(../img/arrow.svg);
                @include bgImgSize(1em);
                border-radius: 1px;
                top: .25em;
                right: 100%;
            }
        }
        span{
            font-size: .75em;
            line-height: 125%;
        }
    }
}


@media screen and (max-width: 992px){
    .page-top{
        padding: .81em 0 1.62em;
    }

    .breadcrumbs{
        display: none;
    }
}
