.quiz{
  padding-bottom: 6.25em;
  margin-top: -1em;
  &__top{
    max-width: 61em;
  }
  &__bottom{
    max-width: 40em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 2.81em;
  }
  &__btn{
    width: 9.375em;
    padding: 0;
    font-weight: 700;
    &:hover{
      path{
        fill: #fff;
      }
    }
    svg{
      width: 1.3125em;
      height: auto;
    }
    &._prev{
      svg{
        margin-right: .44em;
      }
    }
    &._next{
      svg{
        margin-left: .44em;
      }
    }
  }
}

.quiz-step{
  margin-top: 6em;
  &__title{
    line-height: 117%;
  }
  &__variants{
    margin-top: 3.5em;
    @include grid100gap(1.25em);
    .fg{
      max-width: 27.5em;
    }
  }
}

.progress{
  width: 5em;
  position: relative;
  svg{
    width: 100%;
    height: auto;
    transform: rotate(-90deg);
  }
  &__num{
    @include centerAbsolute;
    font-size: 0.875em;
    color: #828282;
  }
}


@media screen and (max-width: 992px){
  .quiz{
    padding-bottom: 3em;
  }
}


@media screen and (max-width: 576px){
  .quiz{
    &__btn{
      width: 3em;
      height: 3em;
      span{
        display: none;
      }
      svg{
        margin: 0 !important;
      }
    }
  }

  .quiz-step{
    margin-top: 3em;
    &__variants{
      margin-top: 1.5em;
    }
  }
}
