.modal{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba($darkBlue, 0.4);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }
    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
}

.modal {
    overflow-y: auto;
    padding: 2em 0;
    text-align: center;
    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog{
        text-align: left;
        vertical-align: middle;
    }
    &__dialog {
        border-radius: 1.25em;
        background: #fff;
        width: 73.75em;
        max-width: calc(100% - 1.875em);
        position: relative;
        z-index: 3;
        display: inline-block;
    }
    &__close {
        z-index: 10;
        border-top: 0;
        background-color: transparent;
        width: .75em;
        @include center;
        position: absolute;
        top: .94em;
        right: 1.25em;
        margin: 0 0 .25em .25em;
        &:hover{
            path{
                opacity: 1;
            }
        }
        svg {
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                stroke: $primary;
            }
        }
    }

    .feedback__inner{
        &:before{
            display: none;
        }
    }
}



@media screen and (max-width: 992px){
    .modal{
        padding: 2em 0;
        &__dialog{
            padding: 1em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog{
            padding: 2em 1em;
            .tooltip__eagle{
                width: 5.5em !important;
                top: 3.5em;
                left: -4.75em;
            }
        }
    }
}
