.auth {
  padding: 5em 0;
  &__inner{
    position: relative;
  }
  &__tabs{
    width: 36.25em;
    max-width: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 50% 50%;
    border-bottom: 1px solid #E0E0E0;
  }
  &__tab{
    height: 3.5em;
    @include center;
    font-weight: 700;
    position: relative;
    cursor: pointer;
    transition: all .3s;
    span{
      font-size: 1em;
      line-height: 125%;
    }
    &:hover{
      color: $primary;
    }
    &._active{
      pointer-events: none;
      &:before{
        opacity: 1;
      }
    }
    &:before{
      content: '';
      position: absolute;
      bottom: -1px;
      left: 0;
      width: 100%;
      height: .25em;
      background: $gradient;
      opacity: 0;
      transition: all .3s;
    }
  }
  .tab-content{
    width: 23.75em;
    max-width: 100%;
    margin: 3em auto 0;
  }
  &__hint{
    position: absolute;
    left: 0;
    bottom: 0;
    .tooltip{
      background-image: url(../img/tooltip8.svg);
      width: 21.875em;
      height: 8.9em;
      &__eagle{
        width: 10.5em;
        margin: -3.25em 0 0 -2.5em;
      }
      &__inner{
        padding: 1.19em 1.25em;
      }
      &__title{
        line-height: 125%;
      }
    }
  }
}

.auth-form{
  &__fields{
    @include grid100gap(1.25em);
  }
  &__nav{
    margin-top: 3.38em;
    display: flex;
    justify-content: center;
  }
}


@media screen and (max-width: 992px){
  .auth{
    padding: 2em 0;
    &__hint{
      display: none;
    }
  }
}
