.career{
  padding: 2em 0 3em;
  background: $darkBlue;
  color: #fff;
  position: relative;
  &__inner{
    display: grid;
    grid-template-columns: 45em calc(100% - 45.5em);
    gap: .5em;
  }
  &__img{
    width: calc(100% + 3.125em);
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    padding-top: 5.7em;
    display: flex;
    flex-direction: column;
  }
  &__nav{
    display: flex;
    margin-top: auto;
    padding-top: 2em;
    .m-btn{
      padding: 0 .81em;
      font-weight: 700;
    }
  }
  .illustration{
    z-index: 1;
  }
  ._illustration1{
    top: 4.56em;
    left: 10.4%;
    width: 8.1875em;
  }
  ._illustration2{
    bottom: 4.375em;
    left: 10.94%;
    width: 21.3125em;
  }
  ._illustration3{
    bottom: 6.125em;
    left: 44.8%;
    width: 6.125em;
  }
  ._illustration4{
    top: 5.625em;
    right: 38%;
    width: 5em;
  }
  ._illustration5{
    right: 10%;
    top: 11.25em;
    width: 4.25em;
  }
  ._illustration6{
    right: 4%;
    bottom: -2em;
    width: 21.25em;
  }
}

.why-can-work{
  .course-info__top{
    max-width: 61.1875em;
  }
}

.career-way{
  .will-suit__top{
    max-width: 100%;
  }
  &__items{
    margin: 2.875em auto 0;
    width: 61em;
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 8.5em / 3));
    gap: 4.25em;
  }
}

.career-item{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
  &__icon{
    height: 3.375em;
    display: block;
    margin-bottom: 2em;
    flex-shrink: 0;
  }
  .text14{
    line-height: 129%;
  }
  &__num{
    position: absolute;
    top: 3em;
    left: 50%;
    transform: translateX(-50%);
    font-weight: 700;
    color: $darkBlue;
    opacity: .2;
    text-shadow: -1px -1px 0 #27AAE1, 1px -1px 0 #27AAE1, -1px 1px 0 #27AAE1, 1px 1px 0 #27AAE1;
    span{
      font-size: 7.5em;
    }
  }
}

.vacancies{
  padding: 4em 0;
}

.partners2{
  padding: 4em 0 6.25em;
}

.vacancies-page{
  padding: 0 0 3.75em;
  margin-top: -2em;
  &__links{
    margin-top: 1.25em;
    display: flex;
    flex-wrap: wrap;
  }
  .faq__items{
    margin-top: 3.625em;
  }
  ._illustration1{
    top: 4em;
  }
  ._illustration2{
    top: -8.75em;
  }
  ._illustration3{
    top: 20em;
    bottom: unset;
  }
}

.vacancy-card{
  &__city{
    margin-left: auto;
  }
  .qa-card__btn{
    margin-left: 5.5em;
  }
  .text{
    h3,
    p,
    li{
      font-size: .875em;
      line-height: 180%;
    }
    h3{
      font-weight: 600;
      @include lastM(0 0 1em);
    }
    b{
      font-weight: 500;
    }
    hr{
      height: 1em;
      opacity: 0;
      display: block;
    }
    ul{
      @include lastM(0 0 1em);
      li{
        padding-left: .8em;
        position: relative;
        &:before{
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          background-image: unset;
          width: unset;
          height: unset;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .career{
    &__inner{
      grid-template-columns: calc(100% - 21em) 20em;
      gap: 1em;
    }
    &__content{
      padding: 0;
    }
    .illustration{
      display: none;
    }
    &__img{
      max-width: 20em;
      width: 100%;
    }
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }

  .career-way{
    &__items{
      grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
      gap: 1.25em;
    }
  }

  .vacancies{
    padding: 2em 0 1em;
  }

  .partners2{
    padding: 1em 0 2em;
  }

  .vacancies-page{
    margin-top: -.5em;
  }
}


@media screen and (max-width: 576px){
  .career{
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .career-way{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .vacancy-card {
    .qa-card__show{
      padding-bottom: 3em;
      position: relative;
    }
    &__city{
      position: absolute;
      bottom: 1.14em;
      left: 1.14em;
    }
  }
}
