.footer{
  padding: 4.37em 0 6.75em;
  background-color: $darkBlue;
  color: #fff;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__info{
    padding-top: 1.875em;
  }
  &__logo{
    display: block;
    width: 8.5em;
    background-color: #fff;
    img{
      width: 100%;
      display: block;
    }
  }
  &__phone{
    margin-top: .94em;
    &-link{
      font-size: 1.125em;
      line-height: 144%;
      font-weight: 600;
    }
  }
  &__address{
    margin-top: .625em;
    .text14{
      line-height: 186%;
    }
  }
  &__email{
    margin-top: .94em;
    &-link{
      font-size: .875em;
      line-height: 186%;
      font-weight: 500;
    }
  }
  .socials{
    margin-top: 1.53em;
  }
  &__c{
    margin-top: 1.85em;
    color: #BDBDBD;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    @include center;
    width: 1.25em;
    @include lastM(0 1.25em 0 0);
    img{
      width: 100%;
      display: block;
      transition: inherit;
    }
    &:hover{
      img{
        transform: translateY(-15%);
      }
    }
  }
}

.footer-nav{
  margin-left: 2em;
  &__title{
    font-size: 1em;
    line-height: 163%;
    font-weight: 700;
  }
  &__links{
    margin-top: 1.25em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    @include lastM(0 0 1em);
    span{
      font-size: .875em;
      line-height: 150%;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1.25em 0;
    &__inner{
      display: block;
    }
    &__info{
      padding-top: 0;
    }
    &__logo{
      display: none;
    }
    &__address,
    &__email{
      margin-top: .75em;
    }
    .socials{
      margin-top: 1.75em;
    }
    &__c{
      margin-top: 2em;
    }
  }

  .socials{
    &__item{
      width: 1.6875em;
    }
  }

  .footer-nav{
    display: none;
  }
}


@media screen and (max-width: 576px){
  .footer{
    .socials{
      padding-right: 1.25em;
      justify-content: space-between;
    }
  }
}
