.teacher-card2{
  border-radius: 1.25em;
  background: #FFF;
  display: flex;
  flex-direction: column;
  &__img{
    flex-shrink: 0;
    @include imgCover(76%);
    border-radius: 1.25em;
  }
  &__content{
    padding: 1em 1.875em 2em;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  &__desc{
    margin-top: 1.31em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 2.3em;
    display: flex;
  }
}

.arrow-link{
  display: flex;
  align-items: center;
  font-weight: 500;
  color: $darkBlue;
  span{
    font-size: .875em;
    line-height: 185%;
  }
  svg{
    width: 1.375em;
    height: auto;
    margin-left: .34em;
  }
  &:hover{
    svg{
      transform: translateX(.5em);
    }
    path{
      fill: $primary;
    }
  }
}

.news-item{
  margin-top: -2em;
  padding-bottom: 6em;
  .text{
    margin-top: 1em;
    max-width: 61.1875em;
    p{
      font-size: 1.125em;
      line-height: 144%;
    }
  }
  .main-catalog__media{
    margin: 5em 0 5em auto;
  }
}

@media screen and (max-width: 992px){
  .news-item{
    margin-top: -1em;
    padding-bottom: 1em;
    .main-catalog__media{
      margin: 2em 0;
    }
    .text{
      p{
        font-size: .875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .teacher-card2{
    &__content{
      padding: 1em;
    }
  }
}
