.course{
  background: $darkBlue;
  color: #fff;
  position: relative;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 33em) 33em;
    position: relative;
  }
  &__img{
    position: relative;
    z-index: 2;
    img{
      width: 100%;
      display: block;
      min-height: 100%;
      object-fit: cover;
    }
  }
  &__content{
    padding: 2.44em .25em 1.875em 0;
  }
  &__top{
    width: 27.5em;
    max-width: 100%;
  }
  &__items{
    display: flex;
    margin-top: 1em;
  }
  &__item,
  &__price{
    padding-top: .44em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: .125em;
      background: $gradient;
    }
  }
  &__item{
    @include lastM(0 1.875em 0 0);
    &:nth-child(2){
      flex-grow: 1;
    }
  }
  &__price{
    display: inline-flex;
    margin-top: 2.19em;
  }
  &__info{
    margin-top: 2.06em;
    @include grid100gap(.75em);
  }
  &__nav{
    display: flex;
    margin-top: 1.5em;
    .m-btn{
      font-weight: 700;
    }
  }
  .illustration{
    z-index: 1;
  }
  ._illustration1{
    top: 1.6em;
    right: 100%;
    margin-right: 1.58em;
    width: 6.1875em;
  }
  ._illustration2{
    bottom: -3.1em;
    right: 100%;
    margin-right: 7.15em;
  }
  ._illustration3{
    top: 11.25em;
    right: 23em;
  }
  ._illustration4{
    right: 5.31em;
    bottom: -1.96em;
  }
  ._illustration5{
    top: 2.81em;
    right: 7.62em;
  }
}

.faq{
  padding: 5em 0 6.25em;
  &__inner{
    position: relative;
    ._illustration1{
      top: 13em;
      left: -6.44em;
      width: 9.3125em;
    }
    ._illustration2{
      top: 0;
      right: -3.88em;
      width: 8.625em;
      bottom: unset;
    }
    ._illustration3{
      right: -6.94em;
      bottom: -3.87em;
      width: 11.25em;
      top: unset;
    }
  }
  &__items{
    margin-top: 1.25em;
    @include grid100gap(1.25em);
  }
}

.qa-card{
  border-radius: 0.375em;
  background: #FFF;
  &._active{
    .qa-card__btn{
      svg{
        transform: rotate(-45deg);
      }
    }
  }
  &__show{
    padding: 1.375em 1.875em;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-size: 1.125em;
    font-weight: 500;
    line-height: 140%;
  }
  &__btn{
    width: 1.0625em;
    @include center;
    flex-shrink: 0;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__hidden{
    padding: 0 1.875em 1.0625em;
    display: none;
  }
  .text{
    p{
      font-size: .875em;
      line-height: 185%;
    }
  }
}

.course-info{
  margin-top: 6em;
  &__top{
    @include grid100gap(1em);
  }
  &__items{
    margin-top: 6em;
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 1.25em * 5 / 6));
    gap: 1.25em;
  }
}

.info-item{
  &__icon{
    height: 2.5em;
    display: flex;
    align-items: flex-end;
    margin-bottom: 1em;
    img{
      max-height: 100%;
      display: block;
    }
  }
}

.teachers-items{
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
  gap: 1.25em;
  margin-top: 2.66em;
  padding-bottom: 3.71em;
}

.teacher-card{
  padding: 1.25em;
  background-color: #fff;
  border-radius: 1.25em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  min-height: 22.5em;
  .avatar{
    background: $gradient;
    padding: .125em;
    width: 8.75em;
    height: 8.75em;
    img{
      border: .5em solid #fff;
    }
  }
  &__info{
    margin-top: 1.56em;
    @include grid100gap(1.25em);
  }
  &__name{
    font-size: 1.125em;
    line-height: 111%;
    font-weight: 700;
  }
  &__desc{
    font-size: .875em;
    line-height: 143%;
  }
}

.course-detail{
  padding: 3em 0;
  &__top{
    max-width: 61.1875em;
    @include grid100gap(1.5em);
    .text18{
      line-height: 144%;
    }
  }
  &__wrap{
    display: flex;
    justify-content: space-between;
    margin-top: 4em;
    position: relative;
  }
  &__left{
    width: 27.5em;
    max-width: 100%;
  }
  &__right{
    display: flex;
  }
  .tooltip{
    width: 21.875em;
    height: 8.61513em;
    background-image: url(../img/tooltip5.svg);
    &__eagle{
      width: 12.25em;
      margin: 2.81em 0 0 .81em;
    }
    &__inner{

    }
    &__title{
      @include truncate(2);
    }
  }
}

.profession{
  padding: 6em 0 3em;
  overflow: hidden;
}

.profession-slider{
  margin-top: 1em;
  .swiper{
    &-slide{
      width: 21em;
      padding-right: 1.25em;
    }
  }
}

.profession-card{
  @include imgCover(66.4%);
  display: block;
  border-radius: 1.25em;
  &:after{
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.20) 0%, rgba(0, 0, 0, 0.20) 100%);
  }
}

.advantages{
  padding: 5.625em 0 3.125em;
  background-color: $darkBlue;
  color: #fff;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    bottom: 99%;
    left: 0;
    width: 100%;
    height: .625em;
    background-image: url(../img/bg.svg);
    background-repeat: repeat-x;
    background-position: 0 0;
    background-size: auto 100%;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
  }
  &__item{
    max-width: 29em;
  }
  &__nav{
    margin-top: 2.56em;
    display: flex;
    justify-content: center;
  }
}

.chess{
  display: grid;
  grid-template-columns: 21.875em calc(100% - 25em);
  justify-content: space-between;
  gap: 1em;
  .tooltip{
    background-image: url(../img/tooltip6.svg);
    width: 21.875em;
    height: 8.9em;
    &__inner{
      padding: 1.875em 1.25em 0;
    }
    &__eagle{
      width: 10.5em;
      display: block;
      margin: -3.25em 0 0 -2.5em;
    }
  }
  &__goals{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: 1.25em;
  }
  &__advantages{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
  }
}

.chess-goal{
  &__icon{
    height: 3.125em;
    display: flex;
    align-items: flex-end;
    margin-bottom: 1em;
  }
  &__info{
    @include grid100gap(.875em);
  }
}


@media screen and (max-width: 992px){
  .course{
    overflow: hidden;
    &__inner{
      grid-template-columns: 100%;
    }
    .illustration,
    &__img{
      display: none;
    }
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }

  .faq{
    padding: 2em 0;
    &__items{
      margin-top: 1em;
      gap: 1em;
    }
    &__inner{
      .illustration{
        display: none;
      }
    }
  }

  .qa-card{
    &__show{
      padding: .75em 1em;
    }
    &__hidden{
      padding: 0 1em .75em;
    }
    &__title{
      font-size: 1em;
    }
  }

  .course-info{
    margin-top: 3em;
    &__top{
      gap: .5em;
    }
    &__items{
      margin-top: 2em;
      grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
      gap: 1.25em;
    }
  }

  .teachers-items{
    grid-template-columns: repeat(2,calc(50% - .625em));
    margin-top: 1.75em;
    padding: 0;
  }

  .course-detail{
    padding: 1.5em 0;
    &__top{
      gap: .75em;
    }
    &__wrap{
      margin-top: 2em;
      flex-direction: column;
      align-items: flex-start;
    }
    &__right{
      order: -1;
      margin: 0 0 1em;
    }
  }

  .profession{
    padding: 2em 0 1em;
  }

  .profession-slider{
    .swiper{
      &-slide{
        width: 15em;
        padding-right: 1em;
      }
    }
  }

  .advantages{
    padding: 3em 0 1em;
    &__items{
      grid-template-columns: 100%;
    }
    &__nav{
      margin-top: 1em;
    }
  }

  .chess{
    grid-template-columns: 100%;
    gap: 2em;
  }
}


@media screen and (max-width: 576px){
  .course-info{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
  }

  .teachers-items{
    grid-template-columns: 100%;
  }

  .course-detail{
    overflow: hidden;
    &__right{

    }
    .tooltip{
      width: 80%;
      &__inner{
        padding-top: .75em;
      }
      &__eagle{
        width: 5em;
        margin-right: -3em;
      }
    }
  }

  .chess{
    &__left{
      font-size: .75em;
    }
    &__advantages{
      grid-template-columns: 100%;
    }
    &__goals{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
  }

  .chess-goal{
    &__info{
      gap: .25em;
    }
    .text24{
      font-size: 1.125em;
    }
    .text14{
      font-size: .75em;
    }
  }
}
