.site-content{
  margin-top: 5em;
  display: grid;
  grid-template-columns: 30em calc(100% - 31.25em);
  gap: 1.25em;
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
}

.event,
.edu-program{
  &__link{
    svg{
      width: .625em;
      height: auto;
      margin-left: .5em;
    }
    &:hover{
      path{
        stroke: #fff;
      }
    }
  }
}

.event{
  grid-column: 1/-1;
  padding: 1.5em 2.5em;
  border-radius: 1.25em;
  background-color: rgba($primary,.6);
  display: flex;
  align-items: center;
  &__date{
    margin-right: 3em;
    text-align: center;
    &-num{
      font-size: 5em;
      font-weight: 700;
    }
    &-month{
      font-size: 1.5em;
      font-weight: 600;
    }
  }
  &__info{
    max-width: 35em;
    padding-right: 1em;
  }
  &__desc{
    margin-top: .5em;
  }
  &__link{
    margin-left: auto;
    height: 3.75em;
  }
}

.site-news,
.edu-program{
  border-radius: 1.25em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &:before{
    filter: blur(.5em);
    height: 100%;
    top: 0;
  }
  &__icon{
    height: 4.85em;
    margin-bottom: 2em;
    img{
      max-height: 100%;
    }
  }
}

.edu-program{
  padding: 2.11em 2.5em 3.125em;
  background: #C5DDFF;
  &__desc{
    margin-top: 2em;
  }
  &__link{
    margin-top: 3em;
  }
}

.site-news{
  background: #B8EFCF;
  padding: 2.1em;
  &._pink{
    background: #FFEEF6;
  }
}

.try-card{
  grid-column: 1/-1;
  display: flex;
  align-items: center;
  padding: 2.5em;
  background: #FFE38E;
  border-radius: 1.25em;
  &__logo{
    width: 8.125em;
    flex-shrink: 0;
    margin-right: 1.88em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    margin-top: 1.5em;
    max-width: 23em;
  }
}

.reviews{
  padding: 3.88em 0 2em;
  background-color: $darkBlue;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 100%;
    height: calc(100% - 2.5em);
    background-image: url(../img/dots2.svg);
    opacity: .6;
  }
  &__wrap{
    overflow: hidden;
    position: relative;
    z-index: 2;
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__title{
    color: #fff;
    text-align: center;
  }
  .illustration{
    z-index: 1;
  }
  ._illustration1{
    bottom: 3.94em;
    left: 0;
    width: 16.4375em;
  }
  ._illustration2{
    top: 3.12em;
    left: 18.24em;
    width: 8.1875em;
  }
  ._illustration3{
    bottom: 8.88em;
    left: 58.25em;
    width: 6.14925em;
  }
  ._illustration4{
    top: -.71em;
    right: 18.87em;
    width: 21.14em;
  }
  ._illustration5{
    bottom: 4.43em;
    right: 18em;
    width: 7.5em;
  }
}

.reviews-slider{
  margin-top: 2.75em;
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-slide{
      width: 25%;
      padding-right: 1.25em;
    }
    &-scrollbar{
      background: #fff;
    }
  }
}

.swiper-scrollbar{
  background: #E0E0E0;
  height: .25em;
  margin-top: 4.75em;
  border-radius: 0;
  &-drag{
    border-radius: 0;
    background: $gradient;
  }
}

.review-card{
  border-radius: 1.25em;
  background: #FFF;
  padding: 1.25em;
  &__top{
    display: flex;
  }
  .avatar{
    margin-right: .625em;
  }
  &__name{
    font-size: .875em;
    line-height: 143%;
    font-weight: 700;
  }
  &__prof{
    font-size: .75em;
    line-height: 120%;
    color: $gray2;
    margin-top: .67em;
  }
  &__desc{
    margin-top: 1.25em;
    font-weight: 500;
    .text14{
      line-height: 157%;
    }
  }
  .rating{
    margin-top: 1.5em;
  }
}

.avatar{
  width: 2.5em;
  height: 2.5em;
  border-radius: 50%;
  flex-shrink: 0;
  display: block;
  img{
    width: 100%;
    height: 100%;
    display: block;
    border-radius: inherit;
  }
}

.rating{
  display: flex;
  align-items: center;
  font-weight: 500;
  color: $gray2;
  img{
    width: 1.375em;
    margin-right: .5em;
    flex-shrink: 0;
  }
}

.welcome{
  padding: 6em 0;
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: 1.25em;
    margin-top: 4em;
  }
}

.welcome-item{
  &__icon{
    height: 3.75em;
    margin-bottom: 1.2em;
    img{
      max-height: 100%;
      display: block;
    }
  }
  &__title{
    font-size: 1.125em;
    line-height: 144%;
    font-weight: 500;
  }
}

.feedback{
  margin-bottom: -2.5em;
  &__inner{
    padding: 2.5em 2.5em 2.5em 3em;
    border-radius: 1.25em;
    background: #FFF;
    display: flex;
    position: relative;
    &:before{
      height: 100%;
      top: 0;
      filter: blur(8px);
    }
    &:hover:before{
      opacity: 0;
    }
    .tooltip{
      margin-left: 12.3em;
      &__eagle{
        position: absolute;
        top: .44em;
        left: -1.44em;
        width: 17.2em;
      }
    }
  }

}

.tooltip{
  flex-shrink: 0;
  background-image: url(../img/tooltip3.svg);
  background-size: 100% auto;
  background-repeat: no-repeat;
  width: 26.11156em;
  height: 12em;
  &__inner{
    height: 100%;
    padding: 1.19em .5em 1em 3.5em;
    overflow: hidden;
  }
  .text{
    margin-top: 1em;
  }
}

.feedback-form{
  margin-left: 2.65em;
  flex-grow: 1;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .625em));
  gap: 1.25em;
  align-self: flex-start;
  &__hint{
    .text10{
      line-height: 140%;
    }
  }
  &__btn{
    align-self: flex-start;
  }
}

._full{
  grid-column: 1/-1;
}


@media screen and (max-width: 992px){
  .site-content{
    margin-top: 1.12em;
    grid-template-columns: repeat(2,calc(50% - .625em));
    &__items{
      grid-column: 1/-1;
    }
  }

  .event{
    grid-column: unset;
    display: block;
    padding: 1.25em;
    position: relative;
    &__date{
      position: absolute;
      top: 1em;
      left: 1.25em;
      &-num{
        font-size: 4em;
      }
      &-month{
        font-size: 1.125em;
        margin-top: .125em;
      }
    }
    &__info{
      padding: 0 0 1.25em;
    }
    &__title{
      font-size: 1.4375em;
      padding-left: 4.34em;
    }
    &__desc{
      margin-top: 2.44em;
      font-size: 1em;
      line-height: 137%;
    }
  }

  .edu-program,
  .site-news{
    padding: 1.25em;
    &__icon{
      margin-bottom: .5em;
    }
    &__title{
      font-size: 1.4375em;
    }
  }

  .edu-program{
    &__desc{
      margin-top: .75em;
    }
    &__link{
      width: 100%;
      margin-top: 1.25em;
    }
  }

  .site-news{
    &__icon{
      margin-bottom: 1.25em;
    }
  }

  .try-card{
    padding: 1.25em;
  }

  .reviews{
    padding: 1.69em 0 3.2em;
    overflow: hidden;
    .illustration,
    &:before{
      display: none;
    }
  }

  .reviews-slider{
    margin-top: .94em;
    .swiper{
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
        width: 15.625em;
        padding-right: .625em;
      }
    }
  }

  .swiper-scrollbar{
    margin-top: 2.25em;
  }

  .review-card{
    height: 100%;
  }

  .welcome{
    padding: 2em 0 1em;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: 1.25em .625em;
      margin-top: 1.25em;
    }
  }

  .feedback{
    margin-top: 2em;
    margin-bottom: 0;
    padding-bottom: 2.44em;
    &__inner{
      padding: 0;
      display: block;
      background-color: transparent;
      &:before{
        display: none;
      }
      .tooltip{
        margin-left: 8em;
        &__eagle{
          left: -3em;
          width: 11em;
        }
      }
    }
  }

  .feedback-form{
    margin: 3em 0 0;
  }
}


@media screen and (max-width: 576px){
  .site-content{
    grid-template-columns: 100%;
    &__items{
      grid-template-columns: 100%;
    }
  }

  .try-card{
    flex-direction: column;
    text-align: center;
    &__logo{
      width: 5em;
      margin: 0 0 1em;
    }
    &__title{
      font-size: 1.375em;
    }
    &__desc{
      margin-top: 1em;
    }
  }

  .welcome{
    .text36{
      max-width: 88%;
    }
  }

  .welcome-item{
    &__title{
      font-size: .75em;
      line-height: 150%;
    }
  }

  .feedback{
    &__inner{
      .tooltip{
        margin-left: auto;
        &__eagle{
          width: 7.5em;
          top: 1.5em;
          left: -3.75em;
        }
      }
    }
  }

  .tooltip{
    background-image: url(../img/tooltip4.svg);
    width: 15.33138em;
    height: 9.375em;
    &__inner{
      padding: 1.25em .12em .5em 1.8em;
    }
    &__title{
      font-size: 1.125em;
    }
    .text{
      margin-top: .5em;
      p{
        font-size: .75em;
      }
    }
  }

  .feedback-form{
    grid-template-columns: 100%;
  }
}
