.events{
  padding-bottom: 6.25em;
}

.event-card2{
  grid-column: span 2;
  border-radius: 1.25em;
  background: #B8EFCF;
  display: grid;
  grid-template-columns: 17.5em calc(100% - 17.5em);
  &__img{
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: 1.25em 0 0 1.25em;
    }
  }
  &__content{
    padding: 1.6875em 1.375em;
    display: flex;
    flex-direction: column;
  }
  &__type{
    margin-bottom: 1.82em;
  }
  &__title{
    font-size: 1.125em;
    line-height: 130%;
    font-weight: 500;
    color: $darkBlue;
  }
  &__desc{
    margin-top: 2em;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 2em;
  }
}

.event-card3{
  border-radius: 1.25em;
  position: relative;
  display: block;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 1.25em;
    &:after{
      background: $darkBlue;
      opacity: .6;
    }
    img{
      border-radius: inherit;
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
  &__content{
    color: #fff;
    position: relative;
    z-index: 2;
    padding: 4em 1.875em;
    @include grid100gap(2.19em);
  }
  &__title{
    font-size: 1.5em;
    line-height: 121%;
    font-weight: 600;
  }
  &__desc{
    .text12{
      line-height: 150%;
    }
  }
}

.event-item{
  .course__nav{
    .m-btn{
      width: 17.875em;
      max-width: 100%;
    }
  }
  &__props{
    margin-top: 3.75em;
    max-width: 36.4375em;
  }
  &__prop{
    padding-top: 1em;
  }
}

.event-catalog{
  .tooltip__inner{
    padding-top: 1.875em;
    padding-left: 1.25em;
  }
  .tooltip__title{
    line-height: 140%;
  }
  .faq__inner ._illustration2{
    right: -3.88em;
    left: unset;
    top: -4.06em;
  }
}

.profession3{
  padding-bottom: 6em;
}


@media screen and (max-width: 992px){
  .events{
    padding-bottom: 1em;
  }

  .profession3{
    padding-bottom: 1em;
  }
}

@media screen and (max-width: 576px){
  .event-card2{
    grid-column: unset;
    grid-template-columns: 100%;
    &__img{
      img{
        border-radius: 1.25em 1.25em 0 0;
      }
    }
    &__content{
      padding: 1.18em;
    }
    &__type{
      margin-bottom: .75em;
    }
    &__desc{
      margin-top: .75em;
    }
    &__bottom{
      padding-top: .75em;
    }
  }
}
