.contacts{
  padding: 0 0 3.125em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 57em) 55.125em;
    gap: 1em;
  }
  &__content{
    max-width: 13em;
  }
  .socials{
    margin-top: 3em;
  }
}

.contact-item{
  @include lastM(0 0 2em);
  &__icon{
    margin-bottom: .625em;
    img{
      max-height: 1.75em;
      max-width: 1.5em;
      display: block;
    }
  }
  &__desc{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-weight: 500;
  }
  &__phone{
    font-size: 1.125em;
    line-height: 144%;
    font-weight: 600;
  }
  &__email{
    font-size: .875em;
    line-height: 186%;
  }
  &__address{
    font-size: .875em;
    line-height: 140%;
  }
}

.map{
  min-height: 100%;
  height: 25.625em;
  width: 100%;
}


@media screen and (max-width: 992px){
  .contacts{
    padding-bottom: 1em;
    &__inner{
      grid-template-columns: 35% calc(65% - 1em);
      gap: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__inner{
      grid-template-columns: 100%;
    }
    .socials{
      margin-top: 2em;
    }
  }

  .contact-item{
    @include lastM(0 0 1em);
  }
}
