.filter-btn{
  display: none;
}

.catalog{
  padding: 1.6em 0 6.25em;
  &__inner{
    display: grid;
    grid-template-columns: 17.5em calc(100% - 18.75em);
    gap: 1.25em;
  }
  &__banner{
    display: block;
    margin-top: 4.375em;
    img{
      width: 100%;
      border-radius: 1.25em;
      display: block;
    }
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
}

.filter{
  &__top,
  &__nav{
    display: none;
  }
}

.filter-group{
  padding-bottom: 1.5em;
  margin-bottom: 1.5em;
  border-bottom: 1px solid #E0E0E0;
  &:last-child{
    border-bottom: none;
    padding-bottom: 0;
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
    font-weight: 500;
  }
  &__bottom{
    margin-top: 1.25em;
  }
  &__checks{
    @include grid100gap(1.25em);
  }
}

.filter-slider{
  @include lastM(0 0 .75em);
  &__item{
    min-height: 1.25em;
    padding: 0 .5em;
    display: flex;
    align-items: center;
    margin-top: 1em;
  }
  &__outputs{
    font-size: .875em;
    line-height: 121%;
    color: $gray;
  }
  .range-slider{
    width: 100%;
    background-color: #D7DCDD;
    border: none;
    box-shadow: none;
    height: .125em;
    border-radius: 0;
    .noUi-connect{
      background: $gradient;
    }
    .noUi-handle{
      box-shadow: none;
      border: none;
      background-color: #F6F7F9;
      background-image: url(../img/radio2.svg);
      @include bgImgSize(100%);
      width: 1.25em;
      height: 1.25em;
      top: -.5625em !important;
      right: -.5625em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
}

.catalog-card{
  display: block;
  padding: 1.5em 1.75em 2.38em;
  border-radius: 1.25em;
  background: #C7EEFF;
  color: $darkBlue;
  &:hover{
    color: $darkBlue;
  }
  &._brown{
    background: #FFCE94;
  }
  &._yellow{
    background: #FFE38E;
  }
  &._purple{
    background: #DDF;
  }
  &._green{
    background: #B8EFCF;
  }
  &._blue{
    background: #C5DDFF;
  }
  &._yellow2{
    background: #FFE9A8;
  }
  &._pink{
    background: #FFEEF6;
  }
  &:before{
    height: 100%;
    top: 0;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__type,
  &__id,
  &__title{
    font-size: 1.125em;
    line-height: 122%;
    font-weight: 500;
  }
  &__info{
    display: flex;
    align-items: center;
    margin-top: 1em;
  }
  &__icon{
    width: 1.6875em;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    margin-top: .75em;
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.625em;
  }
  &__duration{
    color: $black;
  }
  &__sale{
    margin-right: -2.35em;
    font-weight: 700;
    border-radius: 1.25em;
    background: #F11212;
    padding: 0 .5em;
    @include center;
    height: 1.875em;
    min-width: 3.75em;
    color: #fff;
    span{
      font-size: .75em;
    }
  }
}


@media screen and (max-width: 992px){
  .filter-btn{
    padding: .5em 1.125em;
    background-color: #F2F2F2;
    display: flex;
    align-items: center;
    min-height: 3.1875em;
    font-weight: 600;
    color: #151F34;
    img{
      width: 1.0625em;
      margin-right: .31em;
      flex-shrink: 0;
    }
    &__close{
      width: .75em;
      margin-left: auto;
      svg{
        width: 100%;
        height: auto;
      }
    }
  }

  ._filter-open{
    .filter{
      transform: translateX(0);
    }
  }

  .filter{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #fff;
    z-index: 20;
    transform: translateX(-100%);
    transition: all .3s;
    &__top{
      display: block;
    }
    &__scroll{
      height: calc(100% - 3.1875em);
      overflow-y: auto;
      overflow-x: hidden;
      padding: 2.75em .625em;
    }
    &__nav{
      margin-top: 2.5em;
      display: block;
    }
    &__btn{
      width: 100%;
    }
  }

  .catalog{
    padding: 0 0 .5em;
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__banner{
      display: none;
    }
  }

  .catalog-card{
    padding: 1.18em 1.18em .625em;
    &__type,
    &__id,
    &__title{
      font-size: .875em;
    }
    &__icon{
      margin-right: .85em;
    }
    &__bottom{
      margin-top: .69em;
    }
    &__sale{
      margin: 0 0 0 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .filter-btn{
    padding-left: .625em;
    padding-right: .625em;
  }

  .catalog{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
