.header{
  padding: .5em 0;
  @media screen and (min-width: 1280px){
    .container{
      padding-left: 8.75em !important;
    }
  }
  @media screen and (min-width: 993px){
    .container{
      padding: 0 4.5em !important;
    }
  }
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    margin-right: 6.62em;
    flex-shrink: 0;
    width: 9.75em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.menu,
.nav{
  &__link{
    font-weight: 500;
    span{
      font-size: .875em;
      line-height: 121%;
    }
  }
}

.menu{
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__info{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__link{
    margin-top: 1.13em;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link {
    margin-right: 1.06em;
  }
}

.menu-contacts{
  display: flex;
  align-items: center;
  &__address{
    display: flex;
    align-items: center;
    font-weight: 500;
    img{
      width: 1.0625em;
      flex-shrink: 0;
      margin-right: .56em;
    }
    span{
      font-size: .875em;
      line-height: 121%;
      text-decoration: underline;
    }
  }
  &__phone{
    margin-left: 1.12em;
    font-weight: 500;
    color: $darkBlue;
    span{
      font-size: 1em;
      line-height: 163%;
    }
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: .625em 0;
    &__logo{
      width: auto;
      position: relative;
      z-index: 11;
      img{
        height: 2.5em;
        width: auto;
      }
    }
    &._open{
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
      .menu{
        transform: translateX(0);
      }
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #fff;
    z-index: 10;
    display: block;
    transform: translateX(-100%);
    padding: 4em .625em 2em;
    overflow-y: auto;
    &__info{
      align-items: flex-start;
      margin-top: 4em;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      margin: 0 0 1em;
    }
  }

  .burger{
    z-index: 11;
    @include center;
    width: 1.3125em;
    position: relative;
    margin-left: auto;
    img{
      @include centerAbsolute;
      width: 100%;
      display: block;
    }
    ._view2{
      opacity: 0;
      width: 70%;
    }
  }

  .menu-contacts{
    flex-direction: column;
    align-items: flex-start;
    &__phone{
      margin: 1em 0 0;
    }
  }
}
