.about{
  .main__titles{
    text-align: left;
  }
  @media screen and (min-width: 993px){
    .main__content{
      padding-left: 4em;
    }
  }
}

.ceo{
  margin-top: 11em;
  display: grid;
  grid-template-columns: 28.75em calc(100% - 36.25em);
  gap: 7.5em;
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 1.25em;
    }
  }
  &__info{
    margin-top: 1em;
    padding-left: 2.5em;
    @include grid100gap(.75em);
  }
  &__text{
    padding-top: 1.125em;
    font-size: 1.125em;
    line-height: 144%;
    *{
      line-height: inherit;
    }
  }
}

.worth{
  margin-top: 5em;
  &__items{
    margin-top: 2.5em;
  }
}

.programs{
  padding: 5em 0 4em;
  &__items{
    margin-top: 2em;
  }
  &__item{
    padding-top: .81em;
    font-weight: 500;
    .text16{
      line-height: 150%;
    }
  }
}

.partners{
  padding: 4em 0 2.5em;
  &__items{
    margin-top: 2em;
  }
}

.partner-card{
  background-color: #fff;
  border-radius: 1.25em;
  height: 7.5em;
  @include center;
  padding: 1em;
  img{
    max-width: 100%;
    max-height: 100%;
    display: block;
  }
}

.wish{
  padding: 2.5em 0 6em;
  &__items{
    margin-top: 2em;
  }
}

.wish-card{
  padding: 1.38em 1.38em 1.69em;
  border-radius: 1.25em;
  background: #C7EEFF;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &._yellow{
    background: #FFE38E;
  }
  &._purple{
    background: #DDF;
  }
  &._green{
    background: #B8EFCF;
  }
  &__icon{
    margin-bottom: 1.12em;
    height: 3.3125em;
    img{
      height: 100%;
      width: auto;
      display: block;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
    font-weight: 500;
    color: $darkBlue;
  }
}


@media screen and (max-width: 992px){
  .ceo{
    margin-top: 3em;
    grid-template-columns: 20em calc(100% - 21em);
    gap: .5em 1em;
    &__info{
      padding-left: 0;
      margin-top: .5em;
      gap: .5em;
    }
    &__text{
      font-size: .875em;
      padding-top: 0;
    }
  }

  .worth{
    margin-top: 3em;
    &__items{
      margin-top: 1.25em;
    }
  }

  .programs{
    padding: 2.5em 0 1em;
    .knowledge__items{
      margin-top: 2em;
    }
  }

  .partners,
  .wish{
    padding: 1em 0;
    &__items{
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .ceo{
    grid-template-columns: 100%;
  }
}
