._full-screen{
  @media screen and (min-width: 993px){
    font-size: calc(100vw * (16 / 1920));
    .container{
      max-width: 100%;
      padding: 0 6.25em;
    }
  }
}

.main{
  background: $darkBlue;
  display: grid;
  grid-template-columns: calc(100% - 55.5em) 55.5em;
  color: #fff;
  position: relative;
  &__img{
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__content{
    padding: 3.94em 5em 3em 8.75em;
    text-align: right;
    min-height: 30em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 100%;
      height: calc(100% - 2.5em);
      background-image: url(../img/dots3.svg);
      opacity: .6;
    }
  }
  &__titles,
  &__desc{
    position: relative;
    z-index: 2;
  }
  &__titles{
    @include grid100gap(1.94em);
  }
  &__desc{
    margin-top: 4.19em;
  }
  &__illustration{
    position: absolute;
    pointer-events: none;
  }
  ._illustration1{
    left: 9.38em;
    bottom: 10em;
    width: 8.1875em;
  }
  ._illustration2{
    left: 10em;
    bottom: -.71em;
    width: 21.25em;
  }
  ._illustration3{
    bottom: 3.64em;
    left: 40em;
    width: 6.1875em;
  }
  ._illustration4{
    top: 14.31em;
    left: 50.5em;
    width: 19.375em;
  }
  ._illustration5{
    top: 2.19em;
    left: 60em;
    width: 7.5em;
  }
  ._illustration6{
    top: 7.56em;
    right: 43em;
    width: 4.625em;
  }
  ._illustration7{
    width: 5.3125em;
    top: 7em;
    right: 24.37em;
  }
  ._illustration8{
    bottom: -2.91em;
    right: 16.21em;
    width: 8.0625em;
  }
}

._hide-desktop{
  display: none;
}

.main-catalog{
  padding: 6.25em 0;
  &__inner{
    display: grid;
    grid-template-columns: 16.25em calc(100% - 18.75em);
    gap: 2.5em;
  }
  .tab-content{
    margin-top: 2.5em;
  }
  &__links{
    display: flex;
    flex-wrap: wrap;
    margin-top: -1.25em;
  }
  &__media{
    margin: 6em auto 0;
    width: 48.7em;
    max-width: 100%;
    position: relative;
    ._illustration1{
      top: -4.37em;
      left: -5.63em;
      width: 10.3125em;
    }
    ._illustration2{
      bottom: 1.61em;
      left: -16.26em;
      width: 17.5em;
    }
    ._illustration3{
      top: -3.75em;
      right: -15.63em;
      width: 22em;
    }
    ._illustration4{
      bottom: -5.06em;
      right: -9.38em;
      width: 14.375em;
    }
  }
}

.main-tooltip{
  background-image: url(../img/tooltip.svg);
  background-size: 100% auto;
  background-repeat: no-repeat;
  &__inner{
    height: 13.615em;
    padding: 1.25em;
    overflow: hidden;
  }
  .text{
    font-weight: 500;
    b{
      font-weight: 600;
    }
  }
  &__img{
    max-width: 100%;
    display: block;
    width: 10.5em;
    margin: -3em 0 0 -2.5em;
  }
}

.main-tabs{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 1em));
  gap: 1.5em;
  border-bottom: 1px solid $gray5;
}

.main-tab{
  display: flex;
  align-items: center;
  padding: 0 0 .95em 1.17em;
  position: relative;
  transition: all .3s;
  cursor: pointer;
  &:before{
    content: '';
    position: absolute;
    left: 0;
    bottom: -1px;
    width: 10em;
    height: .25em;
    max-width: 100%;
    background: $gradient;
    opacity: 0;
    transition: inherit;
  }
  &:hover{
    &:before{
      opacity: 1;
    }
  }
  &._active{
    pointer-events: none;
    &:before{
      opacity: 1;
    }
  }
  &__icon{
    flex-shrink: 0;
    width: 3.06756em;
    margin-right: 1em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.12em);
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
    font-weight: 700;
  }
  &__desc{
    font-size: .75em;
    line-height: 125%;
    color: $gray2;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.m-link,
.shadow-link{
  position: relative;
  transition: all .3s;
  &:before{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
    border-radius: 0.625em;
    opacity: 0.3;
    background: $gradient;
    filter: blur(.3125em);
    transition: inherit;
  }
  &:hover{
    color: $black;
    &:before{
      opacity: 0.8;
      filter: blur(1.25em);
      transform: translate(.44em,.5em);
    }
  }
}

.m-link{
  margin: 1.25em 1.25em 0 0;
  height: 3.125em;
  @include center;
  padding: 0 1.5em;
  border-radius: 0.375em;
  background: #FFF;
  font-weight: 500;
  &:before{
    height: calc(100% + .26em);
    top: -.13em;
  }
  span{
    font-size: .875em;
    line-height: 120%;
  }
}

.video{
  border-radius: 1.25em;
  display: block;
  position: relative;
  &__img{
    border-radius: inherit;
    width: 100%;
    display: block;
  }
  &__btn{
    z-index: 2;
    @include centerAbsolute;
  }
}

.play-btn{
  width: 6.14em;
  @include center;
  img{
    width: 100%;
    display: block;
  }
}

.illustration{
  position: absolute;
  z-index: -1;
}

.knowledge{
  margin-top: 7em;
  &__items{
    display: flex;
    margin-top: 3em;
    svg{
      height: 5.375em;
      width: auto;
    }
    text{
      font-size: 5.375em;
    }
  }
  &__num{
    height: 6.5625em;
    display: flex;
    align-items: center;
  }
  &__desc{
    font-weight: 500;
  }
}


@media screen and (max-width: 992px){
  ._hide-tablet{
    display: none;
  }

  .main{
    grid-template-columns: 100%;
    &__img{
      order: -1;
      height: unset;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__content{
      padding: 1.875em .625em 2.5em;
      min-height: unset;
      text-align: left;
    }
    &__titles{
      gap: 1.06em;
    }
    &__desc{
      margin-top: 1.31em;
    }
    ._illustration9{
      width: 1.8125em;
      top: 4.61em;
      left: 6.12em;
    }
    ._illustration10{
      top: 1.25em;
      left: 3.5em;
      width: 2.75em;
    }
    ._illustration11{
      top: 6.94em;
      left: -.125em;
    }
    ._illustration12{
      bottom: -.75em;
      left: 3.125em;
      width: 8.9375em;
    }
    ._illustration13{
      top: 5em;
      right: 0;
      width: 3.75em;
    }
    ._illustration14{
      bottom: 2.125em;
      right: 0;
      width: 2.5em;
    }
    ._hide-desktop{
      display: block;
    }
    @media screen and (min-width: 577px){
      font-size: 1.5em;
      ._illustration9,
      ._illustration11{
        display: none;
      }
    }
  }

  .main-catalog{
    padding: 2.5em 0  ;
    overflow: hidden;
    &__inner{
      grid-template-columns: 16.25em calc(100% - 17.25em);
      gap: 1em;
    }
    .tab-content{
      margin-top: 1.25em;
    }
    &__links{
      margin-top: -.625em;
      width: calc(100% + .625em);
    }
    &__media{
      margin-top: 2.5em;
      .illustration{
        display: none;
      }
    }
  }

  .main-tabs{
    gap: .5em;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
  }

  .main-tab{
    flex-direction: column;
    text-align: center;
    padding: 0 0 .41em;
    &:before{
      width: 4.8125em;
    }
    &__icon{
      width: 2.875em;
      height: 2.875em;
      margin: 0;
    }
    &__content{
      gap: .31em;
    }
    &__title{
      font-size: .875em;
      line-height: 121%;
    }
    &__desc{
      font-size: .75em;
      line-height: 125%;
      color: $gray2;
    }
  }

  .m-link{
    padding: 0 .875em;
    margin: .625em .625em 0 0;
  }

  .video{
    border-radius: .375em;
  }

  .play-btn{
    width: 2.5em;
  }

  .knowledge{
    margin-top: 1em;
    &__items{
      margin-top: 0;
      svg{
        width: auto;
        height: 4.875em;
      }
      text{
        font-size: 4em;
      }
    }
    &__item{
      max-width: 33%;
    }
    &__num{
      height: 4.875em;
      margin-bottom: .125em;
    }
    &__desc{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-catalog{
    overflow: hidden;
    &__inner{
      grid-template-columns: 100%;
      gap: 2.44em;
    }
  }

  .main-tooltip{
    background-image: url(../img/tooltip2.svg);
    &__inner{
      height: 10.5em;
      padding: 1em 1.19em;
      br{
        display: none;
      }
    }
    &__img{
      margin: -2.3em 0 0;
      width: 3.9375em;
    }
  }

  .knowledge{
    &__items{
      @include grid100gap(.5em);
    }
    &__item{
      max-width: 95%;
      font-size: .75em;
    }
    &__num{
      //height: 3.5em;
    }
  }
}
