@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba($darkBlue,.6);
    pointer-events: none;
    border-radius: inherit;
  }
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.grid-column2,
.grid-column3,
.grid-column4,
.grid-column5,
.grid-column6{
  display: grid;
  gap: 1.25em;
  grid-auto-flow: dense;
}

.grid-column2{
  grid-template-columns: repeat(2,calc(50% - .625em));
}

.grid-column3{
  grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
}

.grid-column4{
  grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
}

.grid-column5{
  grid-template-columns: repeat(5,calc(20% - 1em));
}

.grid-column6{
  grid-template-columns: repeat(6,calc(100% / 6 - 1.25em * 5 / 6));
}


@media screen and (max-width: 992px){
  .grid-tablet-full{
    grid-template-columns: 100%;
  }

  .grid-tablet2{
    grid-template-columns: repeat(2,calc(50% - .625em));
  }

  .grid-tablet3{
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
  }
}


@media screen and (max-width: 576px) {
  .grid-mob-full {
    grid-template-columns: 100%;
  }

  .grid-mob2{
    grid-template-columns: repeat(2,calc(50% - .625em));
  }
}
