.fg {
  position: relative;
  margin: 0;
  input {
    border: none;
    border-radius: 0.43rem;
    background: rgba(0, 9, 60, 0.10);
    padding: 0 1.36em;
    height: 3.57em;
    width: 100%;
    font-size: .875em;
    color: #000;
    &::placeholder {
      color: $gray2;
    }
    &:focus {
      background: rgba(0, 9, 60, 0.15);
    }
  }
}

.m-radio {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  color: $gray;
  input:checked{
    &~label{
      font-weight: 500;
      &:before{
        opacity: 0;
      }
      &:after{
        opacity: 1;
      }
    }
  }
  label{
    position: relative;
    padding-left: 1.875em;
    min-height: 1.25em;
    display: flex;
    align-items: center;
    span{
      font-size: .875em;
      line-height: 120%;
    }
    &:before,
    &:after{
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 1.25em;
      height: 1.25em;
      @include bgImgSize(100%)
    }
    &:after{
      opacity: 0;
    }
  }
}


.m-radio{
  label{
    &:before{
      background-image: url(../img/radio.svg);
    }
    &:after{
      background-image: url(../img/radio2.svg);
    }
  }
}

.m-check{
  label{
    &:before{
      background-image: url(../img/check.svg);
    }
    &:after{
      background-image: url(../img/check2.svg);
    }
  }
}
