.course2{
  .course__top{
    width: 100%;
    .text16{
      margin-top: .375em;
    }
  }
  .course__items{
    width: 27.5em;
    max-width: 100%;
    margin-top: 2.75em;
  }
  .course__nav{
    .m-btn{
      padding: 0 1.25em;
    }
  }
  .course__img{
    z-index: unset;
  }
  ._illustration6{
    right: 22em;
    bottom: 2.63em;
    width: 8em;
  }
  ._illustration7{
    top: 1.5em;
    right: 18.73em;
    width: 6.15em;
  }
  ._illustration8{
    top: 8.5em;
    right: .625em;
    width: 6.25em;
  }
  ._illustration9{
    top: 3.06em;
    right: 100%;
    margin-right: 2.5em;
    width: 8.125em;
  }
  ._illustration10{
    left: 17.25em;
    bottom: -2.5em;
    width: 21.13em;
  }
}

.main-catalog2{
  .tooltip{
    height: 9.96513em;
    background-image: url(../img/tooltip7.svg);
    &__inner{
      padding-top: 1em;
    }
    &__title{
      line-height: 125%;
    }
  }
}

.advantage-item{
  &__icon{
    height: 2.0625em;
    img{
      height: 100%;
      display: block;
    }
  }
  &__info{
    margin-top: 1em;
    @include grid100gap(.875em);
  }
}

.you-learn{
  margin-top: 3.5em;
  &__items{
    max-width: 58em;
    margin-top: 2em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 3em));
    gap: 1.5em 6em;
    li{
      position: relative;
      font-size: 1em;
      line-height: 162%;
      padding-left: 1.9375em;
      &:before{
        content: '';
        position: absolute;
        background-image: url(../img/list-icon.svg);
        @include bgImgSize(100%);
        width: 1.4375em;
        height: 1.4375em;
        left: 0;
        top: 0;
      }
    }
  }
}

.will-suit{
  padding: 1.5em 0 2.5em;
  background-color: $darkBlue;
  color: #fff;
  &__top{
    text-align: center;
    margin: 0 auto;
    max-width: 23em;
  }
  &__items{
    margin-top: 2.31em;
  }
  &__item{
    position: relative;
    padding-top: 1.31em;
    &:before{
      content: '';
      position: absolute;
      width: 100%;
      height: .125em;
      background: $gradient;
      top: 0;
      left: 0;
    }
    .text24{
      line-height: 108%;
      margin-bottom: .25em;
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1em;
    .m-btn{
      padding: 0 1.31em;
      font-weight: 700;
    }
  }
}

.welcome2{
  padding-bottom: 2.25em;
}

.profession2{
  padding-top: 2.25em;
}

.salary{
  padding: 2.25em 0;
  &__items{
    margin-top: 2em;
  }
}

.salary-item{
  padding: 1.69em 1.875em 2.06em;
  border-radius: 1.25em;
  background: #C5DDFF;
  color: $darkBlue;
  display: flex;
  flex-direction: column;
  &._green{
    background: #B8EFCF;
  }
  &._orange{
    background: #FFCE94;
  }
  &._blue{
    background: #C7EEFF;
  }
  &._yellow{
    background: #FFE38E;
  }
  &__top{
    @include grid100gap(.75em);
  }
  &__desc{
    color: $gray2;
    line-height: 150%;
  }
  &__bottom{
    padding-top: 1.25em;
    margin-top: auto;
    @include grid100gap(.5em);
  }
}

.course-detail2{
  @media screen and (min-width: 577px){
    .tooltip__inner{
      padding: 2.44em 1em 1em 2.5em;
    }
  }
  ._illustration1{
    right: -1.25em;
    bottom: 100%;
    width: 14.625em;
  }
}

.resume{
  padding: 2.625em 0 6.25em;
  &__inner{
    margin-top: 1.5em;
    padding: .125em;
    background: $gradient;
    border-radius: 1.25em;
  }
  &__wrap{
    background-color: #fff;
    padding: 1.75em;
    border-radius: 1.25em;
  }
  &__top,
  &__info{
    display: flex;
    align-items: center;
  }
  &__top{
    justify-content: space-between;
  }
  .avatar{
    background: $gradient;
    padding: .125em;
    width: 6.25em;
    height: 6.25em;
    margin-right: 1.25em;
    img{
      border: .3125em solid #fff;
    }
  }
  &__salary,
  &__content{
    @include grid100gap(.5em);
  }
  &__skills{
    margin-top: 1.25em;
  }
}

._border-top{
  padding-top: 1.12em;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: .125em;
    background: $gradient;
  }
}

.diploma{
  display: flex;
  align-items: center;
  margin-top: 3em;
  &__img{
    width: 9.6875em;
    flex-shrink: 0;
    margin-right: 3.37em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    max-width: 40em;
    @include grid100gap(1em);
    .text16{
      line-height: 162%;
    }
  }
}

.work{
  padding: 5em 0 2.625em;
  &__grid{
    margin-top: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .you-learn{
    margin-top: 1.5em;
    &__items{
      margin-top: 1.25em;
      grid-template-columns: 100%;
      gap: 1em;
    }
  }

  .will-suit{
    &__items{
      margin-top: 1em;
    }
    &__item{
      padding-top: .75em;
    }
  }

  .welcome2{
    padding-bottom: 1.25em;
  }

  .profession2{
    padding-top: 1.25em;
  }

  .salary{
    padding: 1.25em 0;
    &__items{
      margin-top: 1.25em;
    }
  }

  .salary-item{
    padding: 1.25em;
  }

  .course-detail2{
    .illustration{
      display: none;
    }
  }

  .resume{
    padding: 1.25em 0 3em;
    &__wrap{
      padding: 1em;
    }
  }

  .diploma{
    margin-top: 1.25em;
    &__img{
      width: 8em;
      margin-right: 1.25em;
    }
  }

  .work{
    padding: 3em 0 1.25em;
    &__grid{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .you-learn{
    &__items{
      font-size: .75em;
    }
  }

  .resume{
    &__top{
      @include grid100gap(1em);
    }
    .avatar{
      width: 4em;
      height: 4em;
      margin-right: .5em;
      img{
        border-width: .25em;
      }
    }
    &__content{
      gap: .25em;
    }
    &__skills{
      margin-top: .5em;
    }
  }

  .diploma{
    flex-direction: column;
    text-align: center;
    &__img{
      margin: 0 0 1em;
    }
    &__content{
      gap: .5em;
    }
  }
}
