@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 3.8em;
    height: 3.2em;
    border-radius: 0.625em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .875em;
        line-height: 130%;
    }
}

.m-btn-black-outline{
    border: 2px solid $black;
    color: $black;
    &:hover{
        background-color: $black;
        color: #fff;
    }
}

.m-btn-blue-outline{
    border: 2px solid $darkBlue;
    color: $darkBlue;
    &:hover{
        background-color: $darkBlue;
        color: #fff;
    }
}

.m-btn-orange{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: #FFAE4D;
        color: #fff;
    }
}
